hy::before{border:2px solid #353535;display:block;width:100%;height:100%;position:absolute;top:50%;left:50%;transform:translate3d(-50%, -50%, 0) scale(1.015) rotate(0.5deg);border-radius:1% 1% 2% 4% / 2% 6% 5% 4%}.accordion .item img{width:100%;height:100%;object-fit:cover}.accordion .item:hover{flex-grow:10}@media screen and (max-width: 576px){.accordion{flex-direction:column}.accordion .item{height:22.5vh}}

